.materials{
  margin-top: 3.75em;
  @include grid100gap(4.5em);
  .upcoming-event{
    &__inner{
      margin: 0;
    }
  }
}

.material-group{
  &__items{
    margin-top: 1.875em;
    @include grid100gap(1.25em);
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.875em;
  }
}

.doc-card,
.material-card{
  padding: 1.875em;
  background-color: #fff;
  border-radius: 0.625em;
  &:hover{
    color: $black;
  }
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__date{
    color: $grey80;
    font-weight: 500;
  }
}

.material-card{
  display: flex;
  &__img{
    border-radius: 0.375em;
    background-color: $bg;
    width: 5.625em;
    height: 5.625em;
    flex-shrink: 0;
    margin-right: 1.875em;
    @include center;
    padding: .56em;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__content{
    flex-grow: 1;
  }
  &__badge{
    display: flex;
    align-items: center;
    padding: 0 1.12em;
    height: 1.875em;
    border-radius: 1.875em;
    background: #FBEADA;
    color: $primary;
    font-weight: 500;
    &._blue{
      background: $blue20;
      color: $blue;
    }
    &._green{
      background: $green20;
      color: $green;
    }
    span{
      font-size: .875em;
      line-height: 143%;
    }
  }
  &__text{
    margin-top: 1em;
    @include grid100gap(.625em);
  }
}

.new-research{
  padding: 0 0 0 6.81em;
  border-radius: 0.625em;
  background: #FBEADA;
  display: grid;
  grid-template-columns: calc(100% - 38.875em) 38.875em;
  &:before{
    background-image: url(../img/new-research-bg.webp);
    background-position: right top;
  }
  &__img{
    position: relative;
    z-index: 2;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    position: relative;
    z-index: 2;
    max-width: 30em;
    padding: 4.25em 0;
    display: flex;
    flex-direction: column;
  }
  &__text{
    margin-top: 1.25em;
    .text18{
      line-height: 155%;
    }
  }
  &__nav{
    display: flex;
    margin-top: auto;
    padding-top: 1em;
  }
}

.materials-item{
  .event-reg{
    background: #FBEADA;
    position: static;
    transform: unset;
  }
}

.news-nav{
  margin-bottom: 1.875em;
  &__show{
    display: none;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    font-weight: 500;
    color: $grey;
    @include lastM(0 0 .625em);
    span{
      font-size: 1em;
      line-height: 138%;
    }
    &._active{
      color: $primary;
    }
  }
}

.authors{
  margin-bottom: 1.875em;
  border-radius: 0.625em;
  background: #FEFEFE;
  padding: 1.25em;
  &:not(._show-all){
    .authors__item:nth-child(2) ~ .authors__item{
      display: none;
    }
  }
  &._show-all{
    .authors__btn{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
      img{
        transform: rotate(-180deg);
      }
    }
  }
  &__items{
    margin-top: 1.5em;
    @include grid100gap(1.5em);
  }
  &__item{
    display: flex;
    align-items: center;
    .avatar{
      width: 3.5em;
      height: 3.5em;
      margin-right: .5em;
    }
    &-content{
      @include grid100gap(.25em);
    }
  }
  &__nav{
    margin-top: 1.875em;
    display: flex;
  }
  &__btn{
    display: flex;
    align-items: center;
    font-weight: 600;
    font-family: $font2;
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-left: .5em;
      transition: all .3s;
    }
    span{
      font-size: 1em;
      line-height: 150%;
    }
    &:hover{
      opacity: .8;
    }
    ._view2{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){
  .materials{
    margin-top: 2.5em;
    gap: 2.5em;
  }

  .material-group{
    &__items{
      margin-top: 1em;
    }
    &__nav{
      margin-top: 1em;
    }
  }

  .material-card{
    padding: 1.25em;
    &__img{
      margin-right: 1em;
    }
  }

  .new-research{
    padding: 0 0 0 1.875em;
    grid-template-columns: calc(100% - 28em) 28em;
    align-items: center;
    &:before{
      display: none;
    }
    &__content{
      padding: 3.875em 0;
    }
    &__text{
      margin-top: .75em;
    }
    &__nav{
      display: flex;
      margin-top: auto;
      padding-top: 1em;
    }
    &__title{
      font-size: 1.75em;
      line-height: 130%;
    }
  }

  .materials-item{
    .news-item__grid{
      gap: 2.5em;
    }
  }

  .materials-page{
    .footer{
      padding-bottom: 6em;
    }
  }

  .news-nav{
    z-index: 5;
    position: fixed;
    bottom: 1.25em;
    left: 1em;
    width: calc(100% - 2em);
    border-radius: 3.125em;
    border: 1px solid $stroke;
    background: #FEFEFE;
    margin: 0;
    box-shadow: 0px 7px 15px 0px rgba(111, 109, 106, 0.10), 0px 27px 27px 0px rgba(111, 109, 106, 0.09), 0px 61px 37px 0px rgba(111, 109, 106, 0.05), 0px 109px 44px 0px rgba(111, 109, 106, 0.01), 0px 171px 48px 0px rgba(111, 109, 106, 0.00);
    &._active{
      border-radius: 0.625em;
      .news-nav__show img{
        transform: rotate(-180deg);
      }
    }
    &__show{
      cursor: pointer;
      padding: .75em 1.25em;
      display: flex;
      align-items: center;
      justify-content: space-between;
      img{
        transition: all .3s;
        width: 1.5em;
        flex-shrink: 0;
        display: block;
        margin-left: 1em;
      }
    }
    &__hidden{
      padding: 0 1.25em 1.25em;
      display: none;
    }
    &__link{
      @include lastM(0 0 1em);
      span{
        font-size: .875em;
        line-height: 143%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .material-group{
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }

  .doc-card,
  .material-card{
    &__date{
      .text16{
        font-size: .875em;
        line-height: 143%;
      }
    }
    &__text{
      margin-top: .375em;
      .text20{
        font-size: 1.125em;
        line-height: 133%;
      }
      .text16{
        font-size: .875em;
        line-height: 157%;
      }
    }
  }

  .material-card{
    display: block;
    &__img{
      width: 2.75em;
      height: 2.75em;
      padding: .27em;
    }
    &__top{
      display: block;
    }
    &__badges{
      display: flex;
      margin-top: -2.31em;
      padding-left: 3.38em;
    }
    &__date{
      margin-top: 1em;
    }
  }

  .new-research{
    padding: 0 0 7.5em;
    grid-template-columns: 100%;
    &__content{
      padding: 1.875em 1.875em 0;
      position: static;
    }
    &__title{
      font-size: 1.25em;
    }
    &__text{
      .text18{
        font-size: 1em;
        line-height: 150%;
      }
    }
    &__nav{
      padding: 1.875em;
      bottom: 0;
      left: 0;
      width: 100%;
      position: absolute;
      .m-btn{
        width: 100%;
      }
    }
  }
}
