.m-table{
  table{
    width: 100%;
    border-spacing: 0 0;
  }
  th,td{
    padding: 1.25em;
    &:first-child{
      padding-left: 1.875em;
    }
    &:last-child{
      padding-right: 1.875em;
    }
  }
  tbody{
    tr:nth-child(odd){
      td{
        background-color: $primary20;
      }
      &:last-child{
        td{
          &:first-child{
            border-radius: 0 0 0 .625em;
          }
          &:last-child{
            border-radius: 0 0 .625em 0;
          }
        }
      }
    }
  }
  th{
    text-align: left;
    font-weight: 600;
    background-color: $primary;
    color: #fff;
    &:first-child{
      border-radius: 0.625em 0 0 0;
    }
    &:last-child{
      border-radius: 0 0.625em 0 0;
    }
  }
  td{
    font-weight: 500;
    height: 8.75em;
  }
  &__title{
    font-size: .875em;
    line-height: 143%;
  }
  &__mob-title{
    display: none;
  }
}

.bosses-table{
  th,td{
    &:nth-child(2){
      max-width: 21.5em;
    }
    &:last-child{
      max-width: 11.875em;
    }
  }
}

.vacant-table{
  th{
    text-align: center;
  }
  td{
    vertical-align: top;
  }
  th,td{
    padding-left: 1em;
    padding-right: 1em;
    &:nth-child(1){
      max-width: 4.125em;
      width: 4.125em;
      padding-left: 1em;
    }
    &:nth-child(2){
      max-width: 19em;
      width: 19em;
    }
    &:nth-child(7){
      text-align: center;
    }
    &:last-child{
      text-align: center;
      padding-right: 1em;
      //max-width: 11.875em;
    }
  }
}

.table-select{
  position: relative;
  display: none;
  &__show{
    padding: 0 1.5em;
    border-radius: 12.5em;
    height: 2.75em;
    border: 1px solid $stroke;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__current{
    font-size: .875em;
    line-height: 143%;
  }
  &__arrow{
    width: 1.5em;
    flex-shrink: 0;
    margin-left: .5em;
  }
  .m-dropdown{
    //opacity: 1;
    //pointer-events: unset;
  }
  &._open{
    .m-dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
}


@media screen and (max-width: 992px){
  .table-select{
    display: block;
  }

  .m-table{
    &__mob-title{
      display: block;
      margin-bottom: .625em;
      font-size: 1em;
      line-height: 138%;
      font-weight: 700;
      font-family: $font2;
    }
    &__hide-mob{
      display: none !important;
    }
  }

  .mob-table{
    table{
      margin-top: .25em;
    }
    thead{
      display: none;
    }
    &__hide-mob{
      display: none;
    }
    table,
    tbody,
    tr,
    td{
      display: block;
    }
    td{
      text-align: left !important;
      max-width: 100% !important;
      width: 100% !important;
      background-color: transparent !important;
      height: unset;
      border-bottom: 1px solid #E4E0D9;
      padding: 1em 0 !important;
      &:last-child{
        border-bottom: none;
      }
    }
    .boss-row{
      display: none;
      &._active{
        display: block;
      }
    }
  }

  .boss-row{
    td:last-child{
      .m-table__title{
        max-width: 16em;
      }
    }
  }
}
