.docs-page{
  .text40{
    max-width: 22em;
  }
}

.doc-card{
  display: block;
  &__top{
    align-items: center;
  }
  &__img{
    width: 4.125em;
    height: 4.125em;
    border-radius: 0.375em;
    background-color: $bg;
    @include center;
    img{
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__text{
    margin-top: .625em;
    @include grid100gap(.625em);
  }
}

.free-media{
  &__inner{
    margin-top: 1.875em;
    padding-right: 6.81em;
    overflow: hidden;
    border-radius: 0.625em;
    background-color: $primary20;
    display: grid;
    grid-template-columns: calc(100% - 31.6875em) 31.6875em;
    align-items: flex-end;
    &:before{
      background-image: url(../img/free-media-bg.webp);
    }
  }
  &__img{
    position: relative;
    z-index: 2;
    margin-left: 2em;
    max-width: 37em;
    margin-bottom: -9em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    padding: 7.5em 0;
    align-self: center;
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .doc-card{
    padding: 1.25em;
  }

  .free-media{
    .text40{
      display: none;
    }
    &__inner{
      margin-top: 0;
      padding-right: 1.875em;
      grid-template-columns: calc(100% - 23em) 23em;
      &:before{
        display: none;
      }
    }
    &__img{
      margin-bottom: -7em;
    }
    &__content{
      padding: 2.5em 0;
    }
    &__nav{
      margin-top: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .doc-card{
    &__top{
      display: block;
    }
    &__img{
      margin: 0 0 1em;
      width: 2.75em;
      height: 2.75em;
    }
  }

  .free-media{
    &__inner{
      margin-top: 0;
      padding: 1.875em 1.875em 5.875em;
      gap: 1.25em;
      grid-template-columns: 100%;
    }
    &__img{
      margin: 0;
    }
    &__content{
      padding: 0;
      order: -1;
    }
    &__nav{
      margin: 0;
      position: absolute;
      left: 0;
      bottom: 0;
      padding: 1.875em;
      width: 100%;
      .m-btn{
        width: 100%;
      }
    }
  }
}
