.project-modal,
.modal,
.program-modal{
    opacity: 0;
    pointer-events: none;
    width: 100%;
    height: $vh;
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    overflow-y: auto;
    padding-top: 6em;
    &__bg{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(2, 1, 0, 0.44);
        backdrop-filter: blur(7.5px);
        z-index: 10;
        pointer-events: none;
    }
    &__close{
        position: fixed;
        z-index: 12;
        top: 5.75em;
        right: 1.5em;
    }
    &__dialog{
        position: relative;
        z-index: 11;
        border-radius: .625em .625em 0 0;
        background: #F9F7F3;
        padding: 2.5em;
        width: 78em;
        max-width: calc(100% - 12em);
        margin: 0 auto;
    }
}

.modal{
    &._active{
        opacity: 1;
        pointer-events: unset;
        .modal__bg{
            display: block;
        }
        .modal__close{
            display: flex;
        }
    }
    &__dialog{
        padding: 2.25em;
        border-radius: .625em;
        overflow: hidden;
        background-color: #fff;
        min-height: 38em;
    }
    &__tabs{
        .swiper{
            &-slide{
                width: auto;
                padding-right: .625em;
            }
        }
    }
    &__tab{
        margin: 0 !important;
    }
    &__content{
        margin-top: 2.25em;
    }
    .faq-items{
        margin-top: 2.5em;
        gap: 1em;
    }
    .qa-card{
        background-color: $bg;
    }
    .m-table{
        margin-top: 1.25em;
    }
}

.modal-docs{
    @include lastM(0 0 1.25em);
    .text24{
        margin-bottom: .83em;
        max-width: 37em;
        &._full{
            max-width: 100%;
        }
    }
    .text20{
        margin-bottom: .5em;
        max-width: 44em;
    }
    &__items{
        @include grid100gap(.625em);
    }
    &__text{
        line-height: 156%;
        a{
            font-weight: 700;
            text-decoration: underline;
        }
    }
}

.anti-corruption{
    &._open-level2{
        .anti-corruption__level1{
            display: none;
        }
        .anti-corruption__level2{
            display: block;
        }
    }
    &__level2{
        display: none;
    }
    &__tabs{
        margin-top: 1.25em;
        @include grid100gap(1em);
    }
    &__tab{
        display: flex;
        align-items: center;
        padding: .625em 1.25em;
        border-radius: 0.625em;
        background-color: $bg;
        transition: all .3s;
        &:hover{
            cursor: pointer;
            background-color: $primary20;
        }
        &:after{
            content: '';
            flex-shrink: 0;
            display: block;
            width: 1.5em;
            height: 1.5em;
            margin-left: auto;
            background-image: url(../img/arrow-right.svg);
            @include bgImgSize(100%);
        }
    }
    &__back{
        display: flex;
        align-items: center;
        font-weight: 500;
        color: rgba($grey,.5);
        margin-bottom: 1.25em;
        svg{
            width: 1.5em;
            height: auto;
            margin-right: .5em;
        }
        span{
            font-size: .875em;
            line-height: 143%;
        }
        &:hover{
            color: $primary;
            path{
                fill: $primary;
            }
        }
    }
    &__content{
        display: none;
        &._active{
            display: block;
        }
    }
    &__item{
        padding: 1em 1.25em;
        border-radius: 0.625em;
        background-color: $bg;
        display: flex;
        align-items: center;
    }
    .news-article{
        a{
            font-weight: 700;
        }
    }
}

.contacts{
    display: grid;
    grid-template-columns: 31.8125em calc(100% - 31.8125em - 3.75em);
    gap: 3.75em;
    &__items{
        margin-top: 1.25em;
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .5em));
        gap: 1.25em 1em;
        ._full{
            grid-column: 1/-1;
        }
    }
}

.contact-item{
    &__title{
        line-height: 156%;
    }
    &__desc{
        margin-top: .5em;
    }
}

.map{
    height: 26.625em;
    border-radius: .625em;
}

.modal2{
    @media screen and (min-width: 993px){
        text-align: center;
        padding: 0;
        &:after {
            content: '';
            display: inline-block;
            height: 100%;
            vertical-align: middle;
            margin-right: -4px;
        }
        .modal__dialog {
            display: inline-block;
            text-align: left;
            vertical-align: middle;
            width: 38.5625em;
        }
    }
    .modal__dialog{
        min-height: unset;
    }
}

.modal-form{
    margin-top: 1.25em;
    @include grid100gap(1em);
    .m-btn{
        margin-top: .25em;
    }
    &__hint{
        margin-top: .875em;
        max-width: 28em;
    }
}

.iti{
    width: 100%;
    &__selected-flag{
        padding: 0 .625em 0 1.875em;
        background-color: transparent !important;
    }
    &__flag-container{
        &:hover{
            background-color: transparent;
        }
    }
    &__country-list{
        max-width: calc(100vw - 3.25em);
    }
    input{
        padding-left: 5.19em !important;
    }
}

.course-modal{
    .modal__dialog{
        width: 54.25em;
    }
    &__top{
        max-width: 40em;
    }
    .text2{
        margin-top: 1.25em;
    }
    .product-card{
        margin-top: 1.69em;
    }
    &__nav{
        margin-top: 1.875em;
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .5em));
        gap: .5em 1em;
    }
}

.product-card{
    padding: 1.25em;
    border-radius: 0.625em;
    background: rgba(240, 236, 230, 0.60);
    &__top{
        display: grid;
        grid-template-columns: 6.375em calc(100% - 7.375em);
        gap: 1em;
        align-items: center;
    }
    &__img{
        @include imgCover(100%);
        border-radius: .625em;
    }
    &__content{
        max-width: 30.0625em;
        .text18{
            line-height: 155%;
        }
    }
    .edu-prices__items{
        margin-top: 1.25em;
    }
}

.review-modal{
    .modal__dialog{
        width: 58.875em;
    }
    &__text{
        font-size: 1.125em;
        line-height: 150%;
        margin-top: 1.25em;
    }
    .avatar{
        margin-right: 1em;
    }
    .modal__dialog{
        min-height: unset;
        @media screen and (min-width: 993px){
            padding: 1.875em;
        }
    }
}


@media screen and (max-width: 992px){
    .project-modal,
    .program-modal,
    .modal{
        &__close{
            top: 2.75em;
            right: 1.25em;
        }
        &__dialog{
            padding: 1.25em;
            max-width: calc(100% - 2em);
        }
    }

    .modal{
        &__content{
            margin-top: 1.375em;
        }
        .faq-items{
            margin-top: 1.25em;
            gap: .625em;
        }
        .m-table{
            margin-top: 1em;
        }
    }

    .modal-docs{
        @include lastM(0 0 1em);
        .text24{
            margin-bottom: .8em;
        }
        .text20{
            margin-bottom: .875em;
        }
        &__items{
            gap: 1em;
        }
    }

    .anti-corruption{
        &__item{
            padding: .625em 1.25em;
        }
    }

    .contacts{
        grid-template-columns: repeat(2,calc(50% - .625em));
        gap: 1.25em;
    }

    .review-modal{
        &__text{
            font-size: 1em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog{
            max-width: 100%;
            border-radius: .625em .625em 0 0;
        }
    }

    .anti-corruption{
        &__tab{
            padding: .5em 1em;
            position: relative;
            display: block;
            padding-right: 3em;
            &:after{
                position: absolute;
                right: 1em;
                top: 50%;
                transform: translateY(-50%);
            }
        }
    }

    .contacts{
        grid-template-columns: 100%;
        &__items{
            grid-template-columns: 100%;
        }
    }

    .map{
        height: 20em;
    }

    .course-modal{
        &__nav{
            margin-top: 1em;
            grid-template-columns: 100%;
        }
    }

    .product-card{
        padding: 1em;
        &__top{
            grid-template-columns: 100%;
        }
        &__img{
            width: 6.375em;
            height: 6.375em;
            padding: 0;
        }
    }
}
