.survey{
  padding: 1.875em 0;
  flex-grow: 1;
  &._survey-started{
    ._shadow,
    ._shadow2{
      opacity: 0;
    }
    .survey__nav{
      display: flex;
    }
  }
  &__inner{
    position: relative;
  }
  ._shadow,
  ._shadow2{
    position: absolute;
    border-radius: 0.625em;
    height: 100%;
    left: 50%;
    transform: translateX(-50%);
    pointer-events: none;
  }
  ._shadow{
    top: 1.875em;
    width: calc(100% - 3.75em);
    background: rgba(251, 234, 218, 0.50);
    backdrop-filter: blur(44.5px);
    z-index: 2;
  }
  ._shadow2{
    top: 3.75em;
    width: calc(100% - 7.5em);
    background: $primary50;
  }
  &__nav{
    @include center;
    margin-top: 1.875em;
    display: none;
  }
  &__btn{
    background-color: $stroke;
    color: $black;
    &:hover{
      background-color: $primary;
      color: #fff;
    }
    &._disabled{
      pointer-events: none;
      color: $grey80;
    }
  }
  &__pagination{
    margin: 0 2.06em;
    white-space: nowrap;
  }
}

.survey-card{
  position: relative;
  z-index: 3;
  min-height: 33.9375em;
  border-radius: 0.625em;
  background: #FBEADA;
  padding: 3.75em 6.56em;
  &:before{
    background-image: url(../img/survey-bg.webp),url(../img/survey-bg2.webp);
    background-position: 0 0, right top;
  }
  &__inner{
    position: relative;
    z-index: 2;
  }
  &__icon{
    background-color: $primary50;
    width: 6.625em;
    height: 6.625em;
    border-radius: 50%;
    margin: 0 auto 1.875em;
    @include center;
    img{
      width: 4.25em;
    }
  }
  &__text{
    max-width: 62.125em;
    margin: 0 auto;
    text-align: center;
    @include grid100gap(1.25em);
  }
  &__desc{
    font-size: 1.125em;
    line-height: 156%;
    opacity: 0.8;
  }
  &__nav{
    margin-top: 1.875em;
    display: flex;
    justify-content: center;
  }
  &__answers{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: .625em 1em;
  }
}

.survey-last,
.survey-start{
  max-width: 38.625em;
  margin: 0 auto;
  text-align: center;
}

.survey-item{
  display: none;
  &._active{
    display: block;
  }
}

.answer-item{
  border-radius: 0.625em;
  padding: 1.25em;
  background-color: #fff;
  display: flex;
  &__title{
    font-size: 1em;
    line-height: 150%;
  }
  &__rect{
    margin-top: .12em;
    margin-right: .625em;
    flex-shrink: 0;
    width: 1.25em;
    height: 1.25em;
    border: 1px solid $grey50;
    border-radius: 0.3125em;
    background-image: url(../img/check.svg);
    @include bgImgSize(0);
  }
  input{
    &:hover~.answer-item__rect{
      border-color: $primary;
    }
    &:checked~.answer-item__rect{
      background-color: $primary;
      border-color: $primary;
      background-size: 100% auto;
    }
    &[type="radio"]{
      &~.answer-item__rect{
        background-image: unset;
        border-radius: 50%;
      }
      &:checked~.answer-item__rect{
        border-width: .3125em;
        background-color: transparent;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .survey{
    padding: 1.25em 0 2.5em;
    ._shadow{
      top: 1em;
      width: calc(100% - 2em);
    }
    ._shadow2{
      top: 2em;
      width: calc(100% - 4em);
    }
    &__pagination{
      margin: 0 1.25em;
    }
  }

  .survey-card{
    min-height: 30.5625em;
    padding: 1.875em 1.625em 3.56em;
    &:before{
      background-image: url(../img/survey-bg3.webp);
      background-position: 0 0;
    }
    &__icon{
      width: 5em;
      height: 5em;
      img{
        width: 2.625em;
      }
    }
    &__text{
      gap: .75em;
    }
    &__desc{
      font-size: 1em;
      line-height: 137%;
    }
    &__nav{
      margin-top: 4.38em;
    }
    &__answers{
      margin-top: 1.25em;
    }
  }

  .answer-item{
    padding: .75em;
    align-items: center;
    &__rect{
      margin-top: 0;
    }
    &__title{
      font-size: .875em;
      line-height: 143%;
    }
  }
}


@media screen and (max-width: 576px){
  .survey-card{
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
    &__answers{
      grid-template-columns: 100%;
      gap: .5em;
    }
  }
}
