.experts{
  padding: 3.75em 0 0;
}

.experts-info{
  display: grid;
  grid-template-columns: calc(100% - 31.875em) 31.875em;
  justify-content: space-between;
  align-items: flex-start;
  &__content{
    max-width: 39.5625em;
    @include grid100gap(1.25em);
  }
  &__desc{
    font-size: 1.125em;
    line-height: 156%;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: .625em;
    }
  }
}

.experts-content{
  margin-top: 2.5em;
  &__content{
    margin-top: 2.5em;
  }
  .new-research{
    grid-column: 1/-1;
    margin: 5em 0;
    @media screen and (min-width: 993px){
      grid-template-columns: calc(100% - 33.75em) 28em;
    }
  }
  .team-group__grid{
    margin: 0;
  }
}

.team-card2{
  border-radius: .625em;
  overflow: hidden;
  color: #fff;
  &:hover{
    .team-card__content{
      opacity: 1;
    }
  }
  .team-card__side{
    padding: 0;
  }
  .team-card__content{
    opacity: 0;
    transition: all .4s;
    border-radius: inherit;
    padding: 1.25em;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    height: 100%;
    background: linear-gradient(180deg, rgba(112, 99, 84, 0.00) 36.67%, #817362 100%);
  }
}

.expert-events{
  .swiper-slide{
    width: 22.125em;
  }
}

.event-card2{
  .video{
    margin-bottom: .625em;
    &__img{
      height: 13.1875em;
      min-height: unset;
      display: block;
      object-fit: cover;
    }
    &__play{
      width: 3em;
      height: 3em;
      svg{
        width: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .experts{
    padding: 2.5em 0 0;
  }

  .experts-info{
    grid-template-columns: calc(100% - 21.9375em) 20.9375em;
    gap: 1em;
    &__content{
      gap: 1em;
    }
    &__desc{
      font-size: 1em;
      line-height: 150%;
    }
  }

  .experts-content{
    &__content{
      margin-top: 1em;
    }
    .new-research{
      margin: 1.5em 0;
    }
  }

  .expert-events{
    .swiper-slide{
      width: 20.3125em;
    }
  }

  .event-card2{
    .video{
      margin-bottom: .375em;
      &__img{
        height: 11.25em;
      }
      &__play{
        width: 2.5em;
        height: 2.5em;
        svg{
          width: 1.125em;
        }
      }
    }
  }

  .team-card2{
    .team-card__content{
      opacity: 1;
    }
  }
}


@media screen and (max-width: 576px){
  .experts-info{
    grid-template-columns: 100%;
  }

  .experts-content{
    .tabs{
      overflow-x: auto;
      flex-wrap: nowrap;
      @include fullScreen;
    }
    .tab{
      flex-shrink: 0;
    }
    .new-research{
      &__img{
        padding: 0 1.875em;
      }
    }
  }

  .experts{
    .team-group__grid{
      grid-template-columns: 100%;
    }
    .team-card{
      font-size: 1em;
    }
  }
}
