.team-item{
  padding: 1.875em 0 5.375em;
  &__inner{
    display: grid;
    grid-template-columns: 18.75em calc(100% - 18.75em - 2.81em);
    gap: 2.81em;
  }
}

.user{
  position: sticky;
  top: 1em;
  &__img{
    border-radius: 0.625em;
    background-color: $primary20;
    @include imgCover(126%);
  }
  &__btn{
    margin-top: 1.25em;
    width: 100%;
  }
  &__text{
    margin-top: .625em;
  }
}

.worker{
  border-radius: 0.625em;
  background-color: #fff;
  padding: 2.5em;
  overflow: hidden;
  &__contacts{
    margin-top: .625em;
    display: flex;
    align-items: center;
    &-link{
      display: flex;
      align-items: center;
      font-weight: 500;
      @include lastM(0 2.5em 0 0);
      img{
        width: 1.5em;
        margin-right: .375em;
        flex-shrink: 0;
      }
      span{
        font-size: 1em;
        line-height: 138%;
      }
    }
  }
  &__prof{
    margin-top: 1em;
  }
  hr{
    background: #E4E0D9;
    border: none;
    height: .0625em;
    width: 100%;
    display: block;
    margin: 1.25em 0;
  }
}

.vice{
  display: flex;
  align-items: center;
  margin-top: 1.25em;
  padding: 1em 4em 1em 1em;
  border-radius: 0.625em;
  background-color: $bg;
  position: relative;
  &__img{
    width: 2.75em;
    height: 2.75em;
    flex-shrink: 0;
    background-color: #F0E9E1;
    border-radius: 0.375em;
    margin-right: 1em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: inherit;
    }
  }
  .arrow-btn{
    position: absolute;
    top: 50%;
    right: 1em;
    transform: translateY(-50%);
  }
}

.article{
  h3{
    font-size: 1.5em;
    line-height: 133%;
    font-family: $font2;
    font-weight: 700;
    margin-bottom: .42em;
  }
  p{
    font-size: 1.125em;
    line-height: 156%;
    @include lastM(0 0 .55em);
  }
  &__items{
    @include grid100gap(1em);
  }
  &__item{
    display: grid;
    grid-template-columns: 5.5em calc(100% - 5.5em);
    &-num,
    &-text{
      font-size: 1.125em;
      line-height: 156%;
    }
    &-num{
      font-weight: 600;
      color: $primary;
    }
  }
}

.gratitude{
  &__top{
    display: flex;
    align-items: center;
    padding-right: 8em;
    min-height: 3.5em;
    h3{
      margin-bottom: 0;
    }
  }
}

.gratitude-slider{
  margin-top: 1em;
  position: relative;
  .swiper{
    &-slide{
      padding-right: 1em;
      width: 39.5625em;
    }
    &-nav{
      position: absolute;
      right: 0;
      bottom: 100%;
      margin-bottom: 1em;
    }
  }
  .review-card{
    background-color: $bg;
    .avatar{
      background-color: #fff;
    }
  }
}


@media screen and (max-width: 992px){
  .team-item{
    padding: 1em 0 2.5em;
    &__inner{
      grid-template-columns: 18em calc(100% - 19em);
      gap: 2.5em 1em;
    }
  }

  .user{
    &__text{
      margin-top: .5em;
    }
  }

  .worker{
    padding: 1.25em;
    &__contacts{
      margin-top: .5em;
      &-link{
        @include lastM(0 1.5em 0 0);
      }
    }
  }

  .article{
    h3{
      font-size: 1.25em;
      line-height: 130%;
      margin-bottom: .5em;
    }
    p{
      font-size: 1em;
      line-height: 137%;
      @include lastM(0 0 .625em);
    }
    &__item{
      grid-template-columns: 4.5em calc(100% - 4.5em);
      &-num,
      &-text{
        font-size: 1em;
        line-height: 138%;
      }
    }
  }

  .gratitude{
    &__top{
      min-height: unset;
    }
  }

  .gratitude-slider{
    margin-top: .625em;
    .swiper{
      &-nav{
        display: none;
      }
      &-slide{
        width: 20.6875em;
      }
    }

    .review-card{
      padding: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .team-item{
    &__inner{
      grid-template-columns: 100%;
    }
    &__content{
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
    }
  }

  .user{
    &__img{
      padding-bottom: 95%;
    }
  }

  .article{
    &__items{
      gap: .625em;
    }
    &__item{
      grid-template-columns: 100%;
      gap: .625em;
    }
  }
}
