.edu{
  padding: 3.75em 0 6em;
}

.edu-info{
  display: grid;
  grid-template-columns: calc(100% - 15em) 12.1875em;
  justify-content: space-between;
  align-items: flex-start;
  &__content{
    max-width: 58.3125em;
    @include grid100gap(1.25em);
  }
  &__img{
    padding-top: 2.69em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    font-size: 1.125em;
    line-height: 156%;
  }
}

.edu-content{
  margin-top: 3.75em;
  &__items{
    margin-top: 1.25em;
    @include grid100gap(1.25em);
  }
}

.edu-group{
  &._active{
    .program-modal{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.edu-card{
  padding: 1.875em;
  border-radius: 0.625em;
  background-color: #fff;
  display: grid;
  align-items: flex-start;
  grid-template-columns: 13.3125em calc(100% - 13.3125em - 4.63em);
  gap: 4.63em;
  &__img{
    border-radius: .625em;
    @include imgCover(100%);
  }
  &__text{
    @include grid100gap(.625em);
  }
  &__items,
  &__item{
    display: flex;
    align-items: center;
  }
  &__items{
    margin-top: 1.25em;
  }
  &__item{
    @include lastM(0 1.06em 0 0);
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  &__bottom{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-top: 1.88em;
  }
  &__btn{
    width: 13.6875em;
    max-width: 100%;
  }
}

.edu-prices{
  &__title{
    display: none;
  }
  &__items{
    display: flex;
  }
  &__item{
    font-size: 1.25em;
    line-height: 150%;
    font-weight: 600;
    margin-left: .8em;
    &:first-child{
      margin-left: 0;
    }
    &._price1{
      color: $primary;
    }
    &._price2{
      color: $grey;
      text-decoration: line-through;
    }
  }
}

.program-modal{
  .edu-card{
    padding: 2.5em;
    margin: -2.5em 0 0 -2.5em;
    width: calc(100% + 5em);
    background-color: $primary50;
    border-radius: 0.625em 0.625em 0 0;
    grid-template-columns: 11.5em calc(100% - 11.5em - 1.875em);
    gap: 1.875em;
    &:before{
      background-image: url(../img/edu-bg.webp);
      background-position: right top;
    }
    &__content{
      position: relative;
      z-index: 2;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 20.1875em - 3.75em) 20.1875em;
    gap: 3.75em;
    margin-top: 2.5em;
  }
}

.close-btn{
  width: 2.75em;
  height: 2.75em;
  border-radius: 50%;
  @include center;
  background: #F9F7F3;
  box-shadow: 0px 0px 0px 0px rgba(42, 41, 39, 0.10), 0px 2px 4px 0px rgba(42, 41, 39, 0.10), 0px 7px 7px 0px rgba(42, 41, 39, 0.09), 1px 16px 10px 0px rgba(42, 41, 39, 0.05), 1px 29px 11px 0px rgba(42, 41, 39, 0.01), 2px 45px 13px 0px rgba(42, 41, 39, 0.00);
  svg{
    width: 2em;
    height: auto;
  }
  &:hover{
    background: $primary;
    path{
      stroke: #fff;
    }
  }
}

.program-info{
  @include grid100gap(1em);
  @include lastM(0 0 1.875em);
}

.faq-items{
  @include grid100gap(.625em);
}

.qa-card{
  background-color: #fff;
  border-radius: 0.625em;
  &._active{
    .qa-card__btn{
      transform: rotate(-180deg);
    }
  }
  &__show{
    padding: 1em 1.875em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  &__btn{
    padding: 0;
    width: 2em;
    height: 2em;
    border-radius: 50%;
    flex-shrink: 0;
    margin-left: 1.25em;
    svg{
      width: 1.5em;
      height: auto;
    }
  }
  &__hidden{
    padding: 0 1.875em 1em;
    display: none;
  }
}

.text2{
  font-weight: 500;
  ol,ul{
    @include grid100gap(.625em);
  }
  ul{
    li{
      padding-left: 1em;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        border-radius: 50%;
        background-color: $primary;
        width: 0.375em;
        height: 0.375em;
      }
    }
  }
  ul,p{
    @include lastM(0 0 1.25em);
  }
  p,
  li{
    font-size: 1em;
    line-height: 150%;
  }
  a{
    text-decoration-line: underline;
  }
  ol{
    li{
      position: relative;
      padding-left: 2.37em;
      &:before{
        content: attr(data-num)'.';
        position: absolute;
        top: 0;
        left: 0;
        color: $primary;
      }
    }
  }
}

.program-speakers{
  margin-top: 1.875em;
  &__items{
    @include grid100gap(1em);
  }
  .speaker-card{
    grid-template-columns: 9em calc(100% - 10.5em);
    gap: 1.5em;
  }
}

.manager{
  background-color: #fff;
  border-radius: 0.625em;
  padding: 1.25em;
  &__top{
    display: flex;
    align-items: center;
  }
  &__img{
    flex-shrink: 0;
    width: 3.375em;
    height: 3.375em;
    border-radius: 0.3125em;
    background-color: #E6E2DB;
    margin-right: .5em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: inherit;
    }
  }
  &__content{
    @include grid100gap(.125em);
  }
  &__name{
    font-size: 1.125em;
    line-height: 156%;
    font-weight: 700;
    font-family: $font2;
  }
  &__items{
    display: flex;
    flex-direction: column;
    margin-top: 1em;
  }
  &__item{
    display: flex;
    align-items: center;
    @include lastM(0 0 .625em);
    img{
      width: 1.25em;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
}

.program-prices{
  padding-top: 1.75em;
  margin-top: 1.75em;
  border-top: 1px solid $stroke;
  &__btn{
    margin-top: .625em;
    width: 100%;
  }
}

.program-props{
  margin-bottom: 1em;
  padding: 1em;
  border-radius: 0.625em;
  background-color: $grey10;
  @include grid100gap(1em);
}

.program-prop{
  display: flex;
  &__icon{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .625em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.25em)
  }
}

.useful-info{
  padding: 6em 0;
  &__grid{
    margin-top: .875em;
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 1em);
  }
  &__nav{
    display: none;
  }
}

.useful-item{
  width: calc(25% - 1em);
  margin: 1em 1em 0 0;
  flex-shrink: 0;
  padding: 1.875em;
  min-height: 12.5em;
  border-radius: 0.625em;
  background-color: $bg;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  cursor: pointer;
  position: relative;
  @media screen and (min-width: 993px){
    &._medium{
      width: calc(100% / 3 - 1em);
    }
    &._big{
      width: calc(50% - 1em);
      min-height: 14.875em;
      .useful-item__icon{
        display: none;
      }
      .useful-item__title{
        font-size: 1.5em;
        line-height: 133%;
      }
    }
  }
  &__icon{
    width: 2.25em;
    height: 2.25em;
    @include center;
    margin-bottom: 1em;
    border-radius: 50%;
    background: $gradientDesktop;
    img{
      width: 1.5em;
    }
  }
  &__title{
    font-size: 1.125em;
    line-height: 156%;
    font-weight: 700;
    font-family: $font2;
  }
  .arrow-btn{
    margin-top: auto;
  }
  &__img{
    position: absolute;
    bottom: 0;
    right: 1.875em;
    width: 14.8125em;
  }
}


@media screen and (max-width: 992px){
  .edu{
    padding: 2.5em 0;
  }

  .edu-info{
    grid-template-columns: 100%;
    &__content{
      gap: 1em;
    }
    &__desc{
      font-size: 1em;
      line-height: 150%;
    }
    &__img{
      display: none;
    }
  }

  .edu-content{
    margin-top: 2.5em;
    &__items{
      margin-top: 1em;
      gap: 1em;
    }
  }

  .edu-card{
    padding: 1.25em;
    grid-template-columns: 13em calc(100% - 14em);
    gap: 1em;
    &__text{
      gap: .625em;
    }
    &__items{
      margin-top: 1em;
    }
  }

  .program-modal{
    .edu-card{
      padding: 1.25em;
      margin: -1.25em 0 0 -1.25em;
      width: calc(100% + 2.5em);
      grid-template-columns: 10em calc(100% - 11em);
      gap: 1em;
      &:before{
        background-image: url(../img/edu-bg2.webp);
        background-size: 13.5em auto;
      }
      &__content{
        position: relative;
        z-index: 2;
      }
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
      margin-top: 1.25em;
    }
  }

  .close-btn{
    width: 2.25em;
    height: 2.25em;
  }

  .qa-card{
    &__show{
      padding: 1.25em;
      align-items: flex-start;
    }
    &__hidden{
      padding: 0 1.25em 1.25em;
    }
  }

  .program-prices{
    position: fixed;
    bottom: 1em;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 0.625em;
    background: #fff;
    box-shadow: 0px 0px 2.83333px 0px rgba(177, 174, 173, 0.04), 0px 0px 5.86667px 0px rgba(177, 174, 173, 0.06), 0px 0px 11.7px 0px rgba(177, 174, 173, 0.08), 0px 0px 22.93333px 0px rgba(177, 174, 173, 0.10), 0px 0px 42.16667px 0px rgba(177, 174, 173, 0.12), 0px 0px 72px 0px rgba(177, 174, 173, 0.16);
    padding: 1.25em;
    width: calc(100% - 2.5em);
    border: none;
    z-index: 4;
  }

  .useful-info{
    padding: 2.5em 0;
    &__grid{
      margin-top: 0;
    }
  }

  .useful-item{
    padding: 1.25em;
    width: calc(100% / 3 - 1em);
    min-height: 9em;
    .arrow-btn,
    &__img{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .edu-card{
    grid-template-columns: 100%;
    &__img{
      padding-bottom: 52.2%;
    }
    &__item{
      @include lastM(0 .625em 0 0);
      img{
        width: 1.25em;
        margin-right: .25em;
      }
    }
    &__bottom{
      display: block;
    }
    &__btn{
      width: 100%;
    }
  }

  .edu-prices{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: .625em;
    &__title{
      display: block;
    }
    &__item{
      font-size: 1.225em;
      line-height: 156%;
      margin-left: .4em;
    }
  }

  .program-modal{
    &__dialog{
      max-width: 100%;
    }
    .edu-card{
      padding-bottom: 2.63em;
      grid-template-columns: 100%;
      &__img{
        width: 6.375em;
        height: 6.375em;
        padding-bottom: 0;
      }
    }
  }

  .program-speakers{
    margin-top: 1em;
    .speaker-card{
      grid-template-columns: 100%;
      gap: 1em;
    }
  }

  .manager{
    &__top{
      display: block;
    }
    &__content{
      margin-top: .25em;
    }
  }

  .useful-info{
    &:not(._show-all){
      .useful-item{
        &:nth-child(5)~.useful-item{
          display: none;
        }
      }
    }
    &._show-all{
      .useful-info__nav{
        display: none;
      }
    }
    &__nav{
      display: flex;
      justify-content: center;
      margin-top: 1em;
    }
    &__btn{
      width: 100%;
    }
  }

  .useful-item{
    width: calc(100% - 1em);
  }
}
