.fg {
  position: relative;
  input {
    border: 1px solid $bg;
    backdrop-filter: blur(94.5px);
    padding: 0 1.875em;
    border-radius: 3.125em;
    height: 3.5em;
    width: 100%;
    font-size: 1em;
    line-height: 150%;
    background-color: $bg;
    color: $black;
    &::placeholder {
      color: $grey;
    }
    &:focus{
      border-color: $primary;
    }
    &._filled~.fg__clear{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__clear{
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11.5 11.5L4.5 4.5' stroke='%23A1A0A0' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M11.5 4.5L4.5 11.5' stroke='%23A1A0A0' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
    @include bgImgSize(1em);
    width: 1.5em;
    height: 1.5em;
    border-radius: 50%;
    background-color: $grey10;
    position: absolute;
    right: 1.87em;
    top: 1em;
    opacity: 0;
    pointer-events: none;
    &:hover{
      background-color: $primary50;
    }
  }
  &._small{
    input{
      font-size: .875em;
      height: 3.28em;
      padding: 0 2.14em;
    }
    .fg__clear{
      right: 1.25em;
      top: .75em;
    }
  }
}

.files{
  position: relative;
  .file{
    position: absolute;
    top: 50%;
    left: 1.875em;
    transform: translateY(-50%);
    color: $grey;
    z-index: 2;
  }
  &._uploaded{
    .file-input__title{
      opacity: 0;
    }
  }
}

.file-input{
  backdrop-filter: blur(94.5px);
  padding: 0 1.875em;
  border-radius: 3.125em;
  height: 3.5em;
  width: 100%;
  font-size: 1em;
  line-height: 150%;
  background-color: $bg;
  color: $grey;
  display: flex;
  align-items: center;
  justify-content: space-between;
  svg{
    width: 1.5em;
    height: auto;
    flex-shrink: 0;
    margin-left: 1em;
  }
}

.file-input,
.answer-item,
.filter-item{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.check {
  input {
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}


@media screen and (max-width: 992px){
  .fg{
    input{
      font-size: .875em;
      height: 3.28em;
      padding: 0 2.14em;
    }
    &__clear{
      right: 1.25em;
      top: .75em;
    }
  }
}
