.projects-top{
  background-image: url(../img/projects-bg.png);
  background-repeat: no-repeat;
  background-size: 35.375em auto;
  background-position: right top;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 26.375em) 25.375em;
    gap: 1em;
    margin-top: 1.875em;
  }
  &__col{
    display: flex;
    flex-direction: column;
  }
  &__title{
    line-height: 120%;
    span{
      color: $primary;
      padding-left: 2em;
      display: inline-block;
    }
  }
  &__nav{
    display: flex;
    margin-top: auto;
    padding-top: 2em;
  }
  &__images{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3em / 4));
    gap: 1em;
    margin-top: 3.75em;
  }
}

.project-img{
  &__img{
    @include imgCover(100%);
    display: block;
    border-radius: 1em;
  }
}

.projects{
  padding: 6em 0 5em;
  .tab-content{
    margin-top: 2em;
  }
  &__items{
    @include grid100gap(1.25em);
  }
}

.project-group{
  &._open{
    .project-modal{
      opacity: 1;
      pointer-events: unset;
      &__bg{
        pointer-events: unset;
      }
    }
  }
}

.project-card{
  padding: 2.5em;
  background-color: #fff;
  border-radius: .625em;
  display: grid;
  grid-template-columns: calc(100% - 36em) 32.875em;
  justify-content: space-between;
  &__col{
    max-width: 35.625em;
    display: flex;
    flex-direction: column;
  }
  &__badges{
    display: flex;
    flex-wrap: wrap;
    .material-card__badge{
      margin: 0 .625em .625em 0;
      &._blue{
        background: #e2f0fa;
        color: #407cc3;
      }
    }
  }
  &__desc{
    margin-top: 1em;
    opacity: .6;
    .text18{
      line-height: 155%;
    }
  }
  &__nav{
    margin-top: auto;
    padding-top: 2em;
    display: flex;
    align-items: center;
  }
  &__btn{
    padding: 0 4em;
    margin-right: 1.25em;
  }
  .news-slider{
    margin: 0;
    &__img{
      padding-bottom: 81%;
    }
    .swiper-button{
      &-prev{
        left: 1em;
      }
      &-next{
        right: 1em;
      }
    }
  }
}

.project-modal{
  //opacity: 1;
  //pointer-events: unset;
  &__dialog{
    border-radius: .625em;
    background-color: #fff;
  }
  .project-card{
    background-color: transparent;
    padding: 0;
    border-radius: 0;
    &__desc{

    }
    @media screen and (min-width: 993px){
      grid-template-columns: calc(100% - 32.875em) 30.375em;
      &__col{
        max-width: 40em;
      }
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 22.625em) 18.625em;
    gap: 2em 4em;
    margin-top: 3.75em;
  }
  &__col2{
    padding-top: 3em;
  }
  .event-reg{
    &__btn2{
      margin-top: .625em;
      &:not(:hover){
        border-color: rgba(#0B161E,.23);
      }
    }
    &__img{
      width: 4.25em;
    }
  }
}

.event-makers{
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3em / 4));
    gap: 1em;
    margin-top: 1em;
  }
  .support-card{
    margin: 0;
    width: 100%;
    height: 7em;
    background-color: #F9F7F3;
    padding: 1.5em;
    img{
      max-height: 4em;
    }
  }
}

.project-info{
  margin-top: 1.875em;
  &__items{
    margin-top: 1em;
    @include grid100gap(.625em);
    li{
      position: relative;
      padding-left: 2.75em;
      &:before{
        content: '';
        position: absolute;
        width: 1.75em;
        height: 1.75em;
        background-image: url(../img/check2.svg);
        @include bgImgSize(100%);
        top: 50%;
        left: 0;
        transform: translateY(-50%);
      }
      span{
        font-size: 1.125em;
        line-height: 155%;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .projects-top {
    background-image: unset;
    &__grid {
      grid-template-columns: 100%;
      margin-top: 1.25em;
    }
    &__title{
      span{
        padding: 0;
      }
    }
    &__nav{
      padding-top: 1.5em;
    }
    &__images{
      grid-template-columns: repeat(4,calc(25% - 1.5em / 4));
      gap: .5em;
      margin-top: 1.5em;
    }
  }

  .project-img{
    &__img{
      border-radius: .5em;
    }
  }

  .project-card{
    padding: 1.5em;
    grid-template-columns: 100%;
    gap: 1.25em;
    &__col{
      max-width: 100%;
    }
  }

  .project-modal{
    &__grid{
      margin-top: 2em;
      grid-template-columns: 100%;
      gap: 0;
    }
    &__col2{
      padding-top: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .projects-top{
    &__btn{
      width: 100%;
    }
    &__images{
      grid-template-columns: repeat(2,calc(50% - .25em));
    }
  }

  .project-card{
    padding: 1.25em 1em;
    &__title{
      font-size: 1.125em;
    }
    &__desc{
      .text18{
        font-size: 1em;
      }
    }
    &__nav{
      padding-top: 1em;
      flex-direction: column;
    }
    &__btn{
      width: 100%;
      margin: 0 0 1em;
    }
  }

  .event-makers{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
    }
    .support-card{
      margin: 0;
      width: 100%;
      height: 7em;
      background-color: #F9F7F3;
      padding: 1.5em;
      img{
        max-height: 4em;
      }
    }
  }

  .project-info{
    &__items{
      li{
        font-size: .75em;
      }
    }
  }
}
