.news{
  padding: 1.875em 0 0;
  overflow: hidden;
  .tabs{
    margin-top: .75em;
  }
  &__nav{
    margin-top: 1.875em;
    display: flex;
    justify-content: center;
  }
  .search-empty{
    max-width: 27.5em;
  }
}

.news-group{
  margin-top: 2.5em;
  display: grid;
  grid-template-columns: repeat(6,calc(100% / 6 - 1.875em * 5 / 6));
  gap: 1.875em;
  grid-auto-flow: dense;
  @media screen and (min-width: 993px){
    .event-card{
      grid-column: span 2;
      grid-row: span 2;
      &__img{
        height: 14.25em;
        padding: 0;
      }
      &._big{
        grid-column: span 3;
      }
      &._small{
        grid-row: span 1;
        grid-column: span 3;
        display: grid;
        grid-template-columns: 10.1875em calc(100% - 10.1875em - 1.25em);
        gap: 1.25em;
        align-items: center;
        .event-card__img{
          height: 9.875em;
        }
        .event-card__top{
          margin-top: 0;
        }
      }
    }
    .news-subscribe{
      grid-column: span 2;
      grid-row: span 2;
    }
  }
  .upcoming-event__inner{
    grid-column: 1/-1;
    margin: 4.125em 0;
  }
}

.news-subscribe{
  padding: 1.25em 1.25em 1.75em;
  border-radius: 0.625em;
  background: #DFEAF6;
  position: relative;
  &__img{
    position: absolute;
    top: -1.625em;
    right: -1.96em;
    width: 4.4375em;
    display: block;
  }
  &__top{
    padding: 0 1.25em;
    text-align: center;
    @include grid100gap(.625em);
  }
  &__form{
    margin-top: 1.25em;
    @include grid100gap(.625em);
  }
  .fg{
    input{
      border-color: #fff;
      background-color: #fff;
      &:focus{
        border-color: $black;
      }
    }
  }
  &__btn{
    margin-top: .375em;
  }
  &__hint{
    margin-top: .25em;
  }
}


@media screen and (max-width: 992px) {
  .news {
    padding-bottom: 2.5em;
  }

  .news-group{
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
    .upcoming-event__inner{
      margin: 0;
    }
  }

  .news-subscribe{
    &__img{
      right: .5em ;
    }
  }
}


@media screen and (max-width: 576px) {
  .news{
    .tabs{
      margin-top: .25em;
    }
    &__nav{
      margin-top: 1em;
    }
  }

  .news-group{
    margin-top: 1.875em;
    grid-template-columns: 100%;
    gap: 1.25em;
    .event-card{
      border-bottom: none;
    }
  }

  .news-subscribe{
    &__top{
      padding: 0;
      .text20{
        font-size: 1.125em;
        line-height: 156%;
      }
    }
    &__form{
      margin-top: 1em;
      gap: .5em;
    }
    &__img{
      top: -1.38em;
      right: -1.19em;
      width: 2.75em;
    }
    &__btn{
      margin-top: .5em;
    }
  }
}
