.news-item{
  padding: 1.875em 0 6em;
  &__info{
    margin-top: 1.875em;
    max-width: 58.375em;
    @include grid100gap(1.25em);
  }
  &__img{
    display: block;
    border-radius: .625em;
    width: 100%;
    margin-top: 2.5em;
  }
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: calc(100% - 19.625em) 18.625em;
    gap: 1em;
  }
}

.news-top{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__info{
    display: flex;
    align-items: center;
    .event-card__date{
      margin: 0 0 0 .625em;
      position: static;
    }
  }
}

.news-bottom{
  margin-top: 2.5em;
  padding-top: 2.5em;
  border-top: 1px solid $stroke;
  &__info{
    display: flex;
    align-items: center;
  }
  &__source{
    margin-left: 1em;
    font-weight: 600;
    color: $primary;
    &:hover{
      color: $black;
    }
  }
  .socials{
    display: none;
  }
}

.news-article{
  p{
    font-size: 1.125em;
    line-height: 150%;
    @include lastM(0 0 1em);
  }
  hr{
    height: 1.25em;
    display: block;
    opacity: 0;
  }
  .video{
    @include lastM(0 0 1.25em);
  }
  h3,h4,h5{
    line-height: 132%;
    font-weight: 700;
    font-family: $font2;
  }
  h3{
    font-size: 2em;
    @include lastM(0 0 .625em);
  }
  h4{
    font-size: 1.5em;
    @include lastM(0 0 .83em);
  }
  h5{
    font-size: 1.25em;
    @include lastM(0 0 1em);
  }
  img{
    display: block;
    width: 100%;
  }
  a{
    text-decoration: underline;
  }
  &>img{
    @include lastM(0 0 1.125em);
  }
}

.video{
  display: block;
  position: relative;
  &__img{
    display: block;
    width: 100%;
    border-radius: .625em;
  }
  &__play{
    @include centerAbsolute;
    width: 3.75em;
    height: 3.75em;
    box-shadow: 0px 7px 15px 0px rgba(111, 109, 106, 0.10), 0px 27px 27px 0px rgba(111, 109, 106, 0.09), 0px 61px 37px 0px rgba(111, 109, 106, 0.05), 0px 109px 44px 0px rgba(111, 109, 106, 0.01), 0px 171px 48px 0px rgba(111, 109, 106, 0.00);
    background-color: #fff;
    border-radius: 50%;
    @include center;
    &:hover{
      background-color: $primary;
      path{
        fill: #fff;
      }
    }
    svg{
      width: 1.5em;
      height: auto;
    }
  }
}

.docs{
  margin-top: 2.5em;
  &__items{
    margin-top: 1.25em;
    @include grid100gap(1em);
  }
}

.doc-item{
  display: flex;
  align-items: center;
  &__icon{
    width: 2em;
    height: 2em;
    flex-shrink: 0;
    margin-right: 1em;
    border-radius: 50%;
    background: $gradientDesktop;
    @include center;
    img{
      width: 1.25em;
    }
  }
  .arrow-btn{
    margin-left: auto;
    padding-left: 1em;
  }
  .text16{
    max-width: 54em;
  }
}

.news-slider{
  margin-top: 2.5em;
  overflow: hidden;
  position: relative;
  .swiper{
    &-button{
      background-color: #fff;
      border-color: #fff;
      width: 2.75em;
      height: 2.75em;
      top: 50%;
      transform: translateY(-50%);
      margin: 0;
      pointer-events: unset !important;
      &:hover{
        background-color: $primary;
        border-color: $primary;
      }
      &-prev{
        left: 1.25em;
      }
      &-next{
        right: 1.25em;
      }
    }
    &-pagination{
      bottom: 1.25em;
    }
  }
  &__img{
    display: block;
    border-radius: .625em;
    @include imgCover(42.18%);
  }
}

.speakers{
  margin-top: 2.5em;
}

.speakers-slider{
  margin-top: 1.875em;
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-slide{
      width: 50%;
      padding-right: 1.25em;
    }
    &-nav{
      position: absolute;
      right: 0;
      bottom: 100%;
      margin-bottom: 1.25em;
    }
  }
}

.speaker-card{
  display: grid;
  grid-template-columns: 10.8125em calc(100% - 10.8125em - 1.5em);
  gap: 1.5em;
  &__img{
    border-radius: .625em;
    @include imgCover(128%);
  }
  &__content{
    display: flex;
    flex-direction: column;
  }
  &__text{
    margin-top: .625em;
  }
  &__nav{
    padding-top: 1em;
    margin-top: auto;
    display: flex;
  }
}

.support{
  margin-top: 2.5em;
  &__items{
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 1.875em);
  }
}

.support-card{
  margin: 1.25em 1.875em 0 0;
  border-radius: 0.625em;
  background: #FEFEFE;
  padding: 1.25em;
  height: 9.6875em;
  width: 16.25em;
  @include center;
  img{
    max-width: 100%;
    max-height: 5.9625em;
  }
}

.event-reg{
  position: sticky;
  top: 1em;
  border-radius: 0.625em;
  background: #DFEAF6;
  padding: 1.25em;
  &__img{
    width: 5.5625em;
    display: block;
    margin: 0 auto;
  }
  &__info{
    margin-top: .19em;
    margin-left: -.25em;
    width: calc(100% + .5em);
    text-align: center;
    @include grid100gap(.625em);
  }
  .text18{
    line-height: 156%;
  }
  &__desc{
    padding: 0 .25em;
    opacity: 0.6;
  }
  &__btn{
    margin-top: 1.25em;
    width: 100%;
  }
}


@media screen and (max-width: 992px){
  .news-item{
    padding: 2.62em 0 4.12em;
    &__info{
      margin-top: 2.06em;
      @media screen and (min-width: 577px){
        .text40{
          font-size: 2.5em;
        }
      }
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
    }
  }

  .news-top{
    .socials{
      display: none;
    }
  }

  .news-bottom{
    .socials{
      margin-top: 1em;
      display: flex;
    }
  }

  .news-page .footer{
    @media screen and (min-width: 577px){
      padding-bottom: 12em;
    }
  }

  .event-reg{
    @media screen and (min-width: 577px){
      display: flex;
      align-items: center;
      box-shadow: 0px 0px 2.83333px 0px rgba(177, 174, 173, 0.04), 0px 0px 5.86667px 0px rgba(177, 174, 173, 0.06), 0px 0px 11.7px 0px rgba(177, 174, 173, 0.08), 0px 0px 22.93333px 0px rgba(177, 174, 173, 0.10), 0px 0px 42.16667px 0px rgba(177, 174, 173, 0.12), 0px 0px 72px 0px rgba(177, 174, 173, 0.16);
      background-color: #fff;
      position: fixed;
      top: unset;
      z-index: 10;
      bottom: 1em;
      left: 50%;
      transform: translateX(-50%);
      width: calc(100% - 5.25em);
      &__info{
        text-align: left;
        max-width: 25em;
        margin: 0;
      }
      &__img{
        width: 6.5625em;
        flex-shrink: 0;
        margin: 0 .625em 0 0;
      }
      &__desc{
        padding: 0;
      }
      &__btn{
        width: 15.5625em;
        margin: 0 0 0 auto;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .news-item{
    &__info{
      margin-top: 1.25em;
      gap: .625em;
      .text20{
        font-size: .875em;
        line-height: 143%;
      }
    }
    &__img{
      margin-top: 1.875em;
    }
    &__grid{
      margin-top: 1.875em;
      gap: 2.5em;
    }
  }

  .news-top{
    display: block;
    &__info{
      flex-direction: column;
      align-items: flex-start;
    }
    .event-card__date{
      order: -1;
      margin: 0 0 .625em;
    }
  }

  .news-bottom{
    margin-top: 1.875em;
    padding-top: 1.25em;
    &__info{
      flex-direction: column;
      align-items: flex-start;
    }
    &__source{
      margin: .375em 0 0;
    }
    .text18{
      font-weight: 600;
      font-size: 1em;
      line-height: 137%;
    }
  }

  .news-article{
    h3,
    h4{
      font-size: 1.25em;
      @include lastM(0 0 .8em);
    }
    h5{
      font-size: 1.125em;
      @include lastM(0 0 .5em);
    }
    p{
      font-size: 1em;
      @include lastM(0 0 .625em);
    }
    .video{
      @include lastM(0 0 .625em);
    }
  }

  .video{
    &__img{
      min-height: 16.5em;
      object-fit: cover;
    }
    &__play{
      width: 2.5em;
      height: 2.5em;
      svg{
        width: 1em;
      }
    }
  }

  .docs{
    margin-top: 1.875em;
    &__items{
      margin-top: 1em;
    }
  }

  .doc-item{
    display: block;
    position: relative;
    .arrow-btn{
      position: absolute;
      top: 0;
      right: 0;
    }
    &__icon{
      width: 1.75em;
      height: 1.75em;
      margin: 0 0 .375em;
      img{
        width: 1.125em;
      }
    }
  }

  .doc-item2{
    .doc-item__icon{
      margin-bottom: 1em;
    }
  }

  .news-slider{
    margin-top: 1.875em;
    .swiper{
      &-button{
        width: 2em;
        height: 2em;
        &-prev{
          left: 1em;
        }
        &-next{
          right: 1em;
        }
      }
    }
    &__img{
      padding-bottom: 69.2%;
    }
  }

  .speakers-slider{
    margin-top: 1em;
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        width: 100% !important;
        padding: 0;
      }
      &-nav{
        display: none;
      }
      &-wrapper{
        @include grid100gap(1em);
        transform: unset !important;
      }
    }
  }

  .speaker-card{
    grid-template-columns: 100%;
    gap: .625em;
    &__img{
      width: 4em;
      height: 4em;
      padding: 0;
    }
    &__text{
      margin-top: .375em;
    }
    &__nav{
      padding-top: .625em;
    }
  }

  .support{
    margin-top: 1.875em;
    &__items{
      width: calc(100% + 2.5em);
      margin-top: 1em;
      margin-left: -1.25em;
      margin-bottom: -1em;
      padding: 0 1.25em 1em;
      flex-wrap: nowrap;
      overflow-x: auto;
    }
  }

  .support-card{
    margin: 0 1em 0 0;
    width: 9.6875em;
    height: 6.75em;
    flex-shrink: 0;
    img{
      max-height: 3.875em;
    }
  }

  .event-reg{
    padding-left: 2.41em;
    padding-right: 2.41em;
  }
}
